@import "./src/sass/helpers/var";
@import "./src/sass/helpers/animations";

.modal {
  display: flex;
  position: fixed;
  width: 100%;
  height: 100vh;
  background-color: rgba(black, 0.4);
  transition: $transition;
  z-index: 9999;
  opacity: 0;
  visibility: hidden;
  transform: translateX(-100%);
  animation: hideModal 1s ease-in-out forwards;
  pointer-events: none;
  top: 0;
  left: 0;

  &--active {
    animation: showModal 1s ease-in-out forwards;
    pointer-events: all; } }

.form-order {
  display: flex;
  flex-direction: column;
  padding: 3.5% 5%;
  width: 45%;
  margin: auto;
  background-color: $white;
  box-shadow: 0 0 30px 10px rgba($black, 0.5);

  &--wrapper {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    margin: 2.5% 0; }

  &__title {
    font-size: 2.75rem; }

  &__item {
    margin: 2.5% 0;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &:nth-child(2n) {
      align-items: flex-end; }
    label,
    input {
      width: 90%;
      font-size: 1.5rem;
      padding: 1rem 0; }

    input {
      padding: 1rem 1.25rem;
      border: {
        bottom: 1px solid $olive;
        top: none;
        left: none;
        right: none; } } }

  &__btn {
    border: none;
    align-self: center; } }




