}

.instagram {
  background-image: url("../img/sprite.svg#instagram");
}

@media screen and (max-width: 1600px) {
  .social-menu__item {
    margin-right: 1rem;
  }
}

@media screen and (max-width: 1366px) {
  .social-menu {
    width: 70%;
  }
  .social-menu__item {
    width: 4vw;
    height: 4vw;
  }
}

@media screen and (max-width: 768px) {
  .social-menu {
    justify-content: flex-end;
  }
  .social-menu__item {
    width: 5vw;
    height: 5vw;
  }
}

@media screen and (max-width: 480px) {
  .social-menu {
    justify-content: flex-end;
  }
  .social-menu__item {
    width: 12.5vw;
    height: 12.5vw;
  }
}

@keyframes down {
  0% {
    box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.1);
  }
  50% {
    transform: rotateZ(-15deg);
  }
  80% {
    transform: rotateZ(5deg);
  }
  100% {
    box-shadow: none;
  }
}

@keyframes up {
  0% {
    box-shadow: none;
  }
  50% {
    transform: rotateZ(5deg);
  }
  80% {
    transform: rotateZ(-15deg);
  }
  100% {
    box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.1);
  }
}

@keyframes show {
  0% {
    opacity: 0;
    visibility: hidden;
    transform: translateX(100%);
  }
  80% {
    transform: translateX(-2.5%);
  }
  100% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
}

@keyframes hide {
  0% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
  20% {
    transform: translateX(5%);
  }
  100% {
    opacity: 0;
    visibility: hidden;
    transform: translateX(-100%);
  }
}

@keyframes showModal {
  0% {
    opacity: 0;
    visibility: hidden;
    transform: translateX(100%);
  }
  100% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
}

@keyframes hideModal {
  0% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
  20% {
    transform: translateX(5%);
  }
  100% {
    opacity: 0;
    visibility: hidden;
    transform: translateX(-100%);
  }
}

@keyframes scaleItem {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.025);
  }
}

@keyframes unScaleItem {
  0% {
    transform: scale(1.025);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes fade {
  from {
    opacity: 0.1;
  }
  to {
    opacity: 1;
  }
}

.modal {
  display: flex;
  position: fixed;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.4);
  transition: all 0.3s ease;
  z-index: 9999;
  opacity: 0;
  visibility: hidden;
  transform: translateX(-100%);
  animation: hideModal 1s ease-in-out forwards;
  pointer-events: none;
  top: 0;
  left: 0;
}

.modal--active {
  animation: showModal 1s ease-in-out forwards;
  pointer-events: all;
}

.form-order {
  display: flex;
  flex-direction: column;
  padding: 3.5% 5%;
  width: 45%;
  margin: auto;
  background-color: #ffffff;
  box-shadow: 0 0 30px 10px rgba(0, 0, 0, 0.5);
}

.form-order--wrapper {
