@import "./src/sass/helpers/var";

.navigation {
  display: flex;
  width: 100%;
  justify-content: flex-end;
  align-items: center;
  position: relative;

  &--wrapper {
    display: none;

    ul {
      display: flex; } }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: auto;
    width: 100%;

    &:hover,
    &:focus,
    &:active {
      & > ul {
        display: inherit; } } }

  &__link {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    color: $black;
    font-size: 1vw;
    padding: 0.75vw 1vw;
    transition: $transition;
    &:hover,
    &:focus,
    &:active {
      color: $sand-olive; } }

  &--show {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin: 0 0 0 auto;
    height: auto;
    width: 80%;
    padding: 2.5% 0; } }

.submenu {
