.menu {
  display: flex;
  position: relative;
  width: 3rem;
  height: 3rem;
  padding: 0.35rem 0.15rem;
  justify-content: center;
  align-items: center;
  margin-left: 2.5rem;

  &:hover {
    cursor: pointer; }

  span {
    margin: 0 auto;
    position: relative;
    width: 100%;
    height: 6px;
    background-color: $black;
    display: block;
    transition: $transition;

    &:before,
    &:after {
      position: absolute;
      content: '';
      width: 100%;
      height: 6px;
      background-color: $black;
      display: block;
      transition: $transition; }

    &:before {
      margin-top: -12px; }

    &:after {
      margin-top: 12px; } }

  &--toggle {
    span {
      transition-duration: 0s;
      background-color: rgba($black, 0);
      transition-delay: 0.2s;

      &:before,
      &:after {
        transition-property: margin, transform;
        transition-duration: 0.2s;
        transition-delay: 0.2s, 0s;
        margin-top: 0; }

      &:before {
        transform: rotate(45deg); }

      &:after {
        transform: rotate(-45deg); } } } }
